<template>
  <div>
    <div id="map" @mousemove="displayMarker(Position)"></div>
  </div>
  <div class="bookList">
    <h4 style="font-weight:bold">다른 독자들이 읽은, 해당 도서와 같은 장소를 배경으로 한 소설이에요</h4>
    <img v-for="(item, index) in BookIMG" :key="index" :src="require(`@/assets/${BookIMG[index]}`)"/>
  </div>
</template>

<script>
export default {
  name: 'KakaoMap',
  data () {
    return {
      GwangjuBook: [
        '꽃의 나라',
        '소년이 온다'
      ],
      BookIMG: [
        '1980년5월18일.jpg',
        '광주아리랑1.jpg',
        '광주아리랑2.jpg',
        '꽃의나라.jpg',
        '꽃잎처럼.jpg',
        '소년이온다.jpg'
      ],

      Position: [
        [35.1605228, 126.8513870]
      ],

      markers: [],
      infowindow: null
    }
  },
  mounted () {
    if (window.kakao && window.kakao.maps) {
      this.initMap()
    } else {
      const script = document.createElement('script')
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap)
      script.src =
        '//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=2b97a1c59cd9ea0e6b79d98ba75ef2e4'
      document.head.appendChild(script)
    }
  },
  methods: {
    textinput (Book) {
      document.getElementById('textBoxBook').value = Book
    },
    initMap () {
      const container = document.getElementById('map')
      const options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 5
      }

      // 지도 객체를 등록합니다.
      // 지도 객체는 반응형 관리 대상이 아니므로 initMap에서 선언합니다.
      this.map = new kakao.maps.Map(container, options)
    },
    changeSize (size) {
      const container = document.getElementById('map')
      container.style.width = `${size}px`
      container.style.height = `${size}px`
      this.map.relayout()
    },
    displayMarker (markerPositions) {
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null))
      }

      const positions = markerPositions.map(
        (position) => new kakao.maps.LatLng(...position)
      )

      if (positions.length > 0) {
        this.markers = positions.map(
          (position) =>
            new kakao.maps.Marker({
              map: this.map,
              position
            })
        )

        const bounds = positions.reduce(
          (bounds, latlng) => bounds.extend(latlng),
          new kakao.maps.LatLngBounds()
        )

        this.map.setBounds(bounds)
      }
    },
    displayInfoWindow () {
      if (this.infowindow && this.infowindow.getMap()) {
        // 이미 생성한 인포윈도우가 있기 때문에 지도 중심좌표를 인포윈도우 좌표로 이동시킨다.
        this.map.setCenter(this.infowindow.getPosition())
        return
      }

      const iwContent = '<div style="padding:5px;">Hello World!</div>' // 인포윈도우에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
      const iwPosition = new kakao.maps.LatLng(33.450701, 126.570667) // 인포윈도우 표시 위치입니다
      const iwRemoveable = true // removeable 속성을 ture 로 설정하면 인포윈도우를 닫을 수 있는 x버튼이 표시됩니다

      this.infowindow = new kakao.maps.InfoWindow({
        map: this.map, // 인포윈도우가 표시될 지도
        position: iwPosition,
        content: iwContent,
        removable: iwRemoveable
      })

      this.map.setCenter(iwPosition)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
  width: 800px;
  height: 400px;
}

.button-group {
  margin: 10px 0px;
}

button {
  margin: 0 3px;
}
input{
  width: 600px;
  height: 20px;
}
img{
  width: 140px;
  height: 170px;
  padding: 0.5%;
}

.bookList{
  margin-top: 10px;
  float:left
}
</style>
