import { createRouter, createWebHistory } from 'vue-router'
import RelationshipHome from '../views/RelationshipHome.vue'
import PersonEditModal from '../components/PersonEditModal.vue'
import DrawingMap from '../views/DrawingMap.vue'

const routes = [
  {
    path: '/',
    name: 'Relation Diagram Home',
    component: RelationshipHome
  },
  {
    path: '/edit',
    name: 'edit',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/RelationshipEdit.vue')
  },
  {
    path: '/personEdit',
    name: 'testKendo',
    component: PersonEditModal
  },
  {
    path: '/drawingMap',
    name: 'drawingMap',
    component: DrawingMap
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
