<template>
  <body class="background">
    <menu-circle></menu-circle>
      <!--툴 바-->
      <div class="Tool" style="z-index: 2;">
          <ul class = "EditItem">
              <li name="trash" @click="deletebutton" style="list-style:none;"><a href="#"><i class="fa fa-solid fa-trash-can fa-3x" style="color:white"></i></a></li>
              <li name="person" style="list-style:none;" @click="openPersonInfo()"><a href="#"><i class="fa fa-solid fa-circle-plus fa-3x" style="color:white"></i></a></li>
              <li name="palette" style="list-style:none;" @click="Palette"><a href="#"><i class="fa fa-solid fa-palette fa-3x" style="color:white"></i></a></li>
              <li name="refresh" style="list-style:none;" @click="Refresh"><a href="#"><i class="fa fa-solid fa-arrow-rotate-right fa-3x" style="color:white"></i></a></li>
              <li name="Emoticon" style="list-style:none;" @click="AddEmoticon"><a href="#"><i class="fa fa-solid fa-heart fa-3x" style="color:white"></i></a></li>
              <li name="download" style="list-style:none;" @click="DownloadImg"><a href="#"><i class="fa fa-solid fa-download fa-3x" style="color:white"></i></a></li>
          </ul>
      </div>

      <div v-if="showColor" style="position: relative; z-index: 3;">
      <color-picker color="#979797" @color-change="updateColor"
        style="position:fixed; bottom:10%; left:40%;"
        :visible-formats="['rgb', 'hsl']"/>
      </div>

      <kendo-diagram style="position: relative; z-index: 1;" ref="kendo_component"></kendo-diagram>

      <person-edit @close="closeModal" v-if="modal" class="modalbackground">
        <input class= "personName" v-model="personInfo.Name">
        <label class="personImg" for="input-file">
        <img class="personImg" :src="newImgSrc"></label>
         <input id="input-file" type="file" accept="image/png, image/jpeg" v-on:change="upload" style="display:none">
        <div>
          <input name="genderAndAge" class= "genderAndAge" v-model="personInfo.genderAge">
        </div>
        <div>
          <textarea name="information" class= "personInfo" v-model="personInfo.information"></textarea>
        </div>
        <template v-slot:footer>
          <button @click="closeModal">확인</button>
        </template>
    </person-edit>
  </body>
</template>

<script>
import menu from '@/components/menu.vue'
import kendo from '@/components/testKendo.vue'
import personEdit from '@/components/PersonEditModal.vue'
import { ColorPicker } from 'vue-accessible-color-picker'

export default {
  name: 'relation-edit',

  components: {
    'color-picker': ColorPicker,
    'menu-circle': menu,
    'kendo-diagram': kendo,
    // eslint-disable-next-line vue/no-unused-components
    'person-edit': personEdit
  },

  data () {
    return {
      showColor: false,
      modal: false,
      newImgSrc: require('@/assets/profile.png'),
      color: '#979797',
      personInfo: {
        Name: '이름을 입력하세요.',
        image: '@/assets/profile.png',
        genderAge: '성별/나이를 입력하세요.',
        information: '정보를 입력하세요.',
        colorScheme: '#979797'
      }
    }
  },

  methods: {
    refreshAll () {
      this.$router.go()
    },
    deletebutton () {
      this.$refs.kendo_component.diagramData.splice(0, 1)
    },
    Palette () {
      if (this.showColor === false) { this.showColor = true } else if (this.showColor === true) { this.showColor = false }
    },
    Refresh () {
      this.$refs.kendo_component.diagramData = []
    },
    AddEmoticon () {
      this.$refs.kendo_component.diagramData.push(
        {
          id: 'emo'
        }
      )
    },
    DownloadImg () {
      const diagram = this.$refs.kendo_component.diagram1.kendoWidget()
      diagram.exportImage().done(function (data) {
        kendo.saveAs({
          dataURI: data,
          fileName: 'diagram.png',
          proxyURL: 'https://demos.telerik.com/kendo-ui/service/export'
        })
      })
    },
    openPersonInfo () {
      this.modal = true
    },
    closeModal () {
      this.modal = false
      this.AddPerson()
      this.personInfo.Name = '이름을 입력하세요.'
      this.personInfo.image = ''
      this.personInfo.genderAge = '성별/나이를 입력하세요.'
      this.personInfo.information = '정보를 입력하세요.'
      this.personInfo.colorScheme = '#979797'

      this.newImgSrc = require('@/assets/profile.png')
    },
    updateColor (eventData) {
      this.colorScheme = eventData.colors.hex
    },
    AddPerson () {
      this.$refs.kendo_component.diagramData.push(
        {
          Name: this.personInfo.Name,
          image: this.newImgSrc,
          genderAge: this.personInfo.genderAge,
          information: this.personInfo.information,
          colorScheme: this.colorScheme
        }
      )
    },
    upload (e) {
      const file = e.target.files
      const render = new FileReader()

      render.readAsDataURL(file[0])
      render.onload = e => {
        this.newImgSrc = e.target.result
      }
    },
    watch: {
      colors: 'changeBack'
    }
  }
}
</script>

<style scoped>
  body {
  overflow: hidden;
  height: 100%;
  }
  .background{
    height: 100vh;
    margin:0;
    padding:0;
    background-color: #f6f0c9;
  }

  .Tool{
    width:800px;
    height:75px;
    align-content: center;
    position: absolute;
    top:95%;
    left:50%;
    margin-top: -37.5px;
    margin-left: -400px;
    background-color: rgba(220, 83, 0, 0.66);
  }

  li{
    float:left;
    margin-top: 13px;
    margin-left: 40px;
  }

  .EditItem{
    margin-left: 75px;
  }

  .Line{
    width:80px;
    height:10px;
    background-color:deeppink;
    position:fixed; top:200px; left:110px;
  }

  .Text{
    font-weight: bold;
    font-size: 25px;
  }

  .Name{
    font-size: 4px;
  }

  .modalbackground{
    background: rgba(220, 83, 0, 0.66);
  }

  .personName{
    margin-top:40px;
    width: 180px;
    height: 40px;
  }

  .personImg{
    width: 200px;
    height: 200px;
    margin-top:20px;
  }

  .genderAndAge{
    width: 290px;
    margin-top: 30px;
  }

  .personInfo{
    width: 290px;
    height: 260px;
    margin-top: 20px;
  }
</style>
