<template>
<div class="kendo-app">
 <kendo-hierarchicaldatasource ref="remoteDataSourceComponent"
                                  :data="diagramData"
                                  :schema-model-id="'id'">
    </kendo-hierarchicaldatasource>

    <kendo-diagram ref="diagram1"
                   :data-source-ref="'remoteDataSourceComponent'"
                   :layout-type="'layered'"
                   :shape-defaults-visual="visualTemplate"
                   :connection-defaults-stroke-color="'#979797'"
                   :connection-defaults-stroke-width="5"
                   @kendowidgetready="kendowidgetready">
    </kendo-diagram>
  </div>
</template>
<script>
import { Diagram } from '@progress/kendo-diagram-vue-wrapper'
import { HierarchicalDataSource } from '@progress/kendo-datasource-vue-wrapper'
import kendo from '@progress/kendo-ui'

export default {
  components: {
    'kendo-diagram': Diagram,
    'kendo-hierarchicaldatasource': HierarchicalDataSource
  },
  created () {
    this.getList()
  },
  data: function () {
    return {
      diagramColor: '#979797',
      diagramData: []
    }
  },
  methods: {
    getList () {
      this.$http.get('/api/diagram')
        .then((response) => {
          this.diagramData = response.data
        })
    },
    kendowidgetready: function (widget) {
      widget.bringIntoView(widget.shapes)
    },
    visualTemplate: function (options) {
      const dataviz = kendo.dataviz
      const g = new dataviz.diagram.Group()
      const dataItem = options.dataItem

      if (dataItem.id === 'emo') {
        g.append(new dataviz.diagram.Rectangle({
          width: 30,
          height: 30,
          stroke: {
            width: 0
          }
        })
        )
        g.append(new dataviz.diagram.Image({
          source: require('@/assets/heart.png'),
          x: 0,
          y: 0,
          width: 30,
          height: 30
        }))
      } else {
        g.append(new dataviz.diagram.Circle({
          radius: 60,
          stroke: {
            width: 2,
            color: dataItem.colorScheme
          },
          fill: '#e8eff7'
        })
        )

        g.append(new dataviz.diagram.TextBlock({
          text: dataItem.Name,
          x: 35,
          y: 123,
          fill: '#000'
        }))

        g.append(new dataviz.diagram.Image({
          source: dataItem.image,
          x: 25,
          y: 25,
          width: 68,
          height: 68
        }))
      }
      return g
    },
    onDataBound: function (e) {
      const that = e.sender
      setTimeout(function () {
        that.bringIntoView(that.shapes)
      }, 0)
    }
  }
}
</script>

<style scoped>
.kendo-app{
  margin-top: 90px;
  display: block;
  height: 100vh;
  overflow: auto;
  min-height: 80px;
  box-sizing: border-box;
  background-color: #f6f0c9;
}
</style>
